import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { returnmessage, adminData, updateAdminDetails, userNumber, tokenSales, amountCollected, adminTransaction, adminUsers, adminTransactionList, transactdata, adminTickets, checkStageActive, displayStageDetails, viewUserDetails } from './admin.model';
import { ticketDetails, userMessage, message } from './user.model';
import { Message } from '@angular/compiler/src/i18n/i18n_ast';

@Injectable({
  providedIn: 'root'
})
export class AdminserviceService {
  // private admin_url: string = "https://agile-plains-61361.herokuapp.com/admin";
  // private home_url: string = "https://agile-plains-61361.herokuapp.com/home";
  // private admin_url: string = "http://192.168.1.113:3000/admin";
  // private home_url: string = "http://192.168.1.113:3000/home"; 

  
  private home_url: string = "http://ec2-54-89-88-85.compute-1.amazonaws.com:3000/home"; 
  private admin_url: string = "http://ec2-54-89-88-85.compute-1.amazonaws.com:3000/admin";
  // private home_url: string = "http://ec2-54-236-32-232.compute-1.amazonaws.com:3000/home"; 
  // private admin_url: string = "http://ec2-54-236-32-232.compute-1.amazonaws.com:3000/admin";

  private admin;

  constructor(private http: HttpClient) { }

  postSignupData(company){
    return  this.http.post<returnmessage>(this.admin_url+'/admin_signup',company);
  }

  loginadmin(logindata){
    return this.http.post<returnmessage>(this.admin_url+'/admin_login',logindata);
  }
  updateStoStage(stageUpdateInfo){
    return this.http.post<message>(this.admin_url+'/stostages',stageUpdateInfo);
  }
  getAdminStageDetails(stage){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<adminData>(this.admin_url+ '/stage_data',{id: this.admin.adminId, selectedStage: stage})
  }
  sendAdminDetails(adminDetails:updateAdminDetails){
    return this.http.post<any>(this.admin_url+ '/companydetails',adminDetails)
  }
  getUsers(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<userNumber>(this.admin_url+ '/userstatistics',{adminId: this.admin.adminId})
  }
  getTokenSales(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<tokenSales>(this.admin_url+ '/tokensale',{adminId: this.admin.adminId})
  }
  getAmountCollected(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<amountCollected>(this.admin_url+ '/collectedamount',{adminId: this.admin.adminId})
  }
  getAdminTransaction(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<adminTransaction[]>(this.admin_url+ '/recenttransaction',{adminId: this.admin.adminId})
  }
  getUserList(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<adminUsers[]>(this.admin_url+ '/userlist',{adminId: this.admin.adminId})
  }
  getAdminTransactionList(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<adminTransactionList[]>(this.admin_url+ '/transactiondata',{adminId: this.admin.adminId})
  }
  sendTransactionId(sendId){
    return this.http.post<transactdata>(this.admin_url+'/orderinfo',{_id:sendId} );
  }
  getAdminTickets(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<adminTickets[]>(this.admin_url+ '/total_support_tickets',{adminId: this.admin.adminId})
  }
  getAdminChat(id,useremail,adminid){
    return this.http.post<ticketDetails>(this.home_url+ '/ticketdetails',{_id: id,userEmail: useremail,adminId: adminid})
  }
  sendAdminMessage(adminmsg: userMessage){
    return this.http.post<any>(this.home_url+ '/addmessage',adminmsg)
  }
  activatestage(activateStage){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<message>(this.admin_url+ '/stageactive',{adminId: this.admin.adminId, activatedStage: activateStage})
  }
  pausestage(pauseStage){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<message>(this.admin_url+ '/deactivatestage',{adminId: this.admin.adminId, pausedStage:pauseStage})
  }
  closehelpticket(id){
    return this.http.post<message>(this.admin_url+ '/close_ticket',{_id: id})
  }
  checkActiveStage(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<checkStageActive>(this.admin_url+ '/check_stage_active',{adminId: this.admin.adminId})
  }
  displayStageDetails(){
    this.admin = JSON.parse(window.localStorage.getItem('admin'));
    return this.http.post<displayStageDetails[]>(this.admin_url+ '/display_stage_details',{adminId: this.admin.adminId})
  }
  decidetransactionstatus(getdetails: viewUserDetails){
    return this.http.post<message>(this.admin_url+ '/confirm_transaction',getdetails)
  }
  
}
