import { Component, OnInit } from '@angular/core';
import { LayoutComponent } from '../layout.component';

@Component({
  selector: 'app-tokencalculator',
  templateUrl: './tokencalculator.component.html',
  styleUrls: ['./tokencalculator.component.scss']
})
export class TokencalculatorComponent implements OnInit {
 
  constructor(  public getcompanydata: LayoutComponent) { }
  public claimAmt;
  public checkAmt;
  tokenValue: number;
  currentCurrency: string= 'eth';
  iconName: string= 'ethereum';
  baseTokenValue:number= 0; 
  ngOnInit() {
    if(this.getcompanydata.allcompanydetailsreturn)
      this.setTokenValue();
    
  }
  setcurrency(currentCurrency: string){
    console.log(currentCurrency);
    
    this.currentCurrency=currentCurrency;
    if(this.currentCurrency=='eth')
    this.iconName='ethereum';
    else
    this.iconName='btc';
    this.setTokenValue();
  }
  setTokenValue(){
    if(this.currentCurrency == 'eth')
    this.tokenValue= this.getcompanydata.allcompanydetailsreturn.tokenValuePerETH;
    if(this.currentCurrency == 'btc')
    this.tokenValue= this.getcompanydata.allcompanydetailsreturn.tokenValuePerBTC;
    this.calculateBaseTokenValue();
    
  }
  calculateBaseTokenValue(){
    if(this.currentCurrency == 'eth')
      this.tokenValue= this.getcompanydata.allcompanydetailsreturn.tokenValuePerETH;
    if(this.currentCurrency == 'btc')
      this.tokenValue= this.getcompanydata.allcompanydetailsreturn.tokenValuePerBTC;
    if(!Number(this.checkAmt))
    {
      this.baseTokenValue=0;
      return;
    }
    this.baseTokenValue= (this.checkAmt * this.tokenValue)
  }
  
}
