import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sessionexpired',
  templateUrl: './sessionexpired.component.html',
  styleUrls: ['./sessionexpired.component.scss']
})
export class SessionexpiredComponent implements OnInit {

  constructor(private router: Router) { }
  fn(){
    window.localStorage.removeItem('company');
  }
  ngOnInit() {
    var company = JSON.parse(window.localStorage.getItem('company'))
    var person = JSON.parse(window.localStorage.getItem('user'));
    if(person || !company){
      this.router.navigate(['/**'])
    }
  }

}
