import { Component, OnInit } from '@angular/core';
import { AuthenticateService } from 'src/app/authenticate.service';
import { companyname, companydetails, company } from 'src/app/user.model';
import { routerNgProbeToken } from '@angular/router/src/router_module';
import { Router, NavigationExtras } from '@angular/router';

@Component({
  selector: 'app-companytoken',
  templateUrl: './companytoken.component.html',
  styleUrls: ['./companytoken.component.scss']
})
export class CompanytokenComponent implements OnInit {
  

  constructor(private companydata: AuthenticateService, private router: Router) { }

  public companyidentity: companydetails=null;
  public companylist: Array<companyname>;
  company: company;
  ngOnInit() {
    this.companydata.getcompanyinfo().subscribe(res=>{
      this.companylist = res;
      console.log(res);
    })
  }

  tokeninfo(companyid: string){
    this.company={
      companyId: companyid
    }
    window.localStorage.setItem('company',JSON.stringify(this.company));
    console.log(companyid);
    // let navigationExtras:  NavigationExtras= {
    //   queryParams: { 'id': companyid }
    // };
    // this.companydata.sendId(companyid).subscribe(res =>{});
    this.router.navigate([''], );
  }
}
