import { Component, OnInit } from '@angular/core';
import { AuthenticateService } from 'src/app/authenticate.service';
import { Ticket, companyname, companyinfo } from 'src/app/user.model';
import { MatDialog, MatDialogConfig, MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-addticket',
  templateUrl: './addticket.component.html',
  styleUrls: ['./addticket.component.scss']
})
export class AddticketComponent implements OnInit {
  newticket: Ticket;
  public messag = [];
  subject: string;
  message: string;
  query: string;
  companyId: string;
  email: string;
  companytrack: string;
  flag='1';
  constructor(private addtix: AuthenticateService, private dialog:MatDialog, private router: Router, public dialogRef: MatDialogRef<AddticketComponent>, private toastr:ToastrService) { }
  
  sendTicket(){
    this.newticket = {
      subject: this.subject,
      message: this.message,
      query: this.query,
      companyId: this.companytrack,
      email: JSON.parse(window.localStorage.getItem('user')).email,
      flag: this.flag
    }
    this.addtix.getticket(this.newticket).subscribe(res =>{
      this.messag = res;
      this.router.navigateByUrl('/viewprof', {skipLocationChange: true}).then(()=>
				this.router.navigate(["/helpdesk"]));
    } )
    this.openToastr();
    this.openSuccess();
  }
  openToastr(){
    this.toastr.success('Ticket Successfully Created!');
  }
  openSuccess(){
    this.dialogRef.close();
    
    //this.router.navigate(['/helpdesk'])
    
  }
  public companydetail: Array<companyinfo>;
  ngOnInit() {
    this.addtix.addcompanyinfo().subscribe(res=>{
      this.companydetail = res;
      console.log(res)
    } )
  }

}










