import { Component, OnInit,Inject } from '@angular/core';
import { profdetail, sendPassword,  userData, updateUserDetails, msg, Mobilecode, mobiledetails, countryCode, Mobileotp, otpReturns } from '../../user.model'

import { AuthenticateService } from 'src/app/authenticate.service';
import { Router } from '@angular/router';
import {FormControl, FormGroupDirective, NgForm, Validators,ReactiveFormsModule} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';
import { MatSnackBar } from '@angular/material';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { MatDialog, MatDialogConfig, MatDialogRef,MAT_DIALOG_DATA } from '@angular/material';
import { CountryCodesService } from 'src/app/services/country-codes.service';
import { invalid } from '@angular/compiler/src/render3/view/util';
import { UploadService } from 'src/app/services/upload.service';
import { HttpEventType, HttpResponse } from '@angular/common/http';


export class ethErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

export class passworderrormatcher implements ErrorStateMatcher{
	isErrorState(control: FormControl| null, form: FormGroupDirective | NgForm | null): boolean {
	  const isSubmitted= form && form.submitted;
	  return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted))
	}
  }


@Component({
	selector: 'app-profile',
	templateUrl: './profile.component.html',
	styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
	profiledetail: profdetail;
	public var = "0";
	public userCheck = "0";
	public toggle = "0";
	name: string;
	email: string;
	number: string;
	nationality: string;
	dob: Date;
	pass: sendPassword
	cemail: string;
	old_password: string;
	new_password: string;
	confirm_password: string;
	receivingAddress: string;
	passverify: msg;
	returnmsg: msg;
	countryCode: string;
	city: string;
	postalCode: string;
	requestId: string;
	country: string;
	addressLine1: string;
	addressLine2: string;
	private message = [];
	myControl = new FormControl();
	countryCodes:Array<countryCode>;
	countryNames: Array<string>=[];
	dialCodes: Array<string>=[];
	filteredOptions: Observable<string[]>;
	otp: string;
	otpmsg: string;
	public otpReturn: otpReturns;
	public mobileVar = "0";
	selectedFiles: FileList;
  	currentFileUpload: File;
  	progress: { percentage: number } = { percentage: 0 };
	constructor(private profileservice: AuthenticateService, private router:Router,private snackBar: MatSnackBar, private countryservice: CountryCodesService, private dialog: MatDialog,private uploadService: UploadService) {}
	public x = " ";
  	public passmatch:number = 0;

  // ======================================================
  // * form control for receiving address(regax)
  // ======================================================

	ethFormControl = new FormControl('', [
		Validators.pattern(/^0x[a-fA-F0-9]{40}$/)
	]);
	ethmatcher = new ethErrorStateMatcher();
	verify: Mobilecode
	mobdetails:mobiledetails

	 // ======================================================
  // * Function for mobile verification to send the otp
  // ======================================================

	sendOtp(){
		this.verify = {
			email: this.email,
			countryCode: this.countryCode,
			number: this.number
		};
		this.profileservice.verifymobile(this.verify).subscribe(res=>
			{
				this.mobdetails = res;
				console.log(res)
				if(res.message == '0'){
					console.log("invalid");
				}else if(res.message == '1'){
					this.openDialog();
				}
			});
		

	}
	

	 // ======================================================
  // * Function to change the password
  // ======================================================

	verification: Mobileotp
	// otpVerification(){
		
	// }
	changePassword() {
		this.pass = {
			oldPassword: this.old_password,
			newPassword: this.new_password,
			cnewPassword: this.confirm_password,
			cemail: JSON.parse(window.localStorage.getItem('user')).email
		};
		this.profileservice.passwordChange(this.pass).subscribe(res =>{
			this.passverify = res;
			console.log(res);
			if(res.msg=='0')
			{
				this.openSnackBar(res.err,"");
			}
			if(res.msg=='1')
			{
				this.openSnackBar("password updated succesfully","");
			}
		})

	}

	 // ======================================================
  // * Function to change the profile details and send those to backend
  // ======================================================
	
	userDetails: updateUserDetails;
	sendUserInfo() {
		this.userDetails = {
			name: this.name,
			dob: this.dob,
			city: this.city,
			postalCode: this.postalCode,
			email: JSON.parse(window.localStorage.getItem('user')).email,
			receivingAddress: this.receivingAddress,
			country: this.country,
			addressLine1: this.addressLine1,
			addressLine2: this.addressLine2
		}
		
		this.profileservice.sendUserInformation(this.userDetails).subscribe(res => { 
			this.returnmsg=res;
			console.log(res);
			if(this.returnmsg.msg!='0'){
				console.log("RewindTime");
				this.router.navigateByUrl('/viewprof', {skipLocationChange: true}).then(()=>
				this.router.navigate(["/profile"]));
			}
			else{
				this.openSnackBar("Refund address invalid","");
			}
		})
		this.toggle = "0";
		
		
	}
	personaldata() {
		this.var = "0";
	}
	settings() {
		this.var = "1";
	}
	password() {
		this.var = "2";
	}
	userInfo: userData;
	formtoggle() {
		this.toggle = "1";
	}
	
	 // ======================================================
  // * Display the profile details of the user on the loading of page
  // ======================================================

	ngOnInit() {
		this.filteredOptions = this.myControl.valueChanges
		.pipe(
		  startWith(''),
		  map(value => this._filter(value))
		);
		this.profileservice.getUserDetails().subscribe(res => {
			this.userInfo = res;
			this.dob = this.userInfo.dob;
			this.city = this.userInfo.city;
			this.postalCode = this.userInfo.postalCode;
			this.country = this.userInfo.country;
			this.addressLine1=this.userInfo.addressLine1;
			this.addressLine2 = this.userInfo.addressLine2;
			console.log(res);
		});
		console.log("test")
		this.countryCodes = this.countryservice.getCountryCodes();
		// .subscribe(res=>this.countryCode = res);
		console.log(this.countryCodes);
		for(var i=0; i<this.countryCodes.length; i++){
			this.countryNames.push(this.countryCodes[i].code + ' '+'(' + this.countryCodes[i].dial_code + ')');
		}
		
		console.log(this.dialCodes);
	} 

	 // ======================================================
  // *	Function to upload the file
  // ======================================================
	selectFile(event) {
		this.selectedFiles = event.target.files;
	  }
	  filereturn;
	  upload() {
		this.progress.percentage = 0;
	
		this.currentFileUpload = this.selectedFiles.item(0);
		this.uploadService.pushFileToStorage(this.currentFileUpload).subscribe(event => {
			// this.filereturn = event;
		  if (event.type === HttpEventType.UploadProgress) {
			this.progress.percentage = Math.round(100 * event.loaded / event.total);
		  } else if (event instanceof HttpResponse) {
			console.log('File is completely uploaded!');
		  }
		});
	
		this.selectedFiles = undefined;
	  }
		private _filter(value: string): string[] {
		const filterValue = value.toLowerCase();
	
		return this.countryNames.filter(option => option.toLowerCase().includes(filterValue));
	}
	 

	 // ======================================================
  // * Function to open the snaack bar
  // ======================================================


	openSnackBar(message: string, bonuspercent: string) {
		this.snackBar.open(message, bonuspercent, {
		  duration: 4000,
		});
	  }

	   // ======================================================
  // * Function to open the mobile otp dialog
  // ======================================================

	  openDialog(): void {
		const dialogRef = this.dialog.open(mobileOtpDialog, {
		  width: '500px',
		  height: 'auto',
		  disableClose: true,
		  data: {otp: this.otp,msg: this.otpmsg}
		});
	
		dialogRef.afterClosed().subscribe(result => {
		  console.log('The dialog was closed'+result);
		  this.otp = result;
		  if(result!=null){
			this.verification = {
				code: this.otp,
				requestId: this.mobdetails.requestId,
				email: JSON.parse(window.localStorage.getItem('user')).email,
				number: this.number
			};
			this.profileservice.verifyOtp(this.verification).subscribe(res=> {
				this.otpReturn = res;
				if(res.message == '1'){
					this.openSnackBar('Mobile number verified',"");
					this.mobileVar = '1';
					this.router.navigateByUrl('/viewprof', {skipLocationChange: true}).then(()=>
				this.router.navigate(["/profile"]));
				}else{
					this.otpmsg = "Invalid OTP, Please retry";
					this.openDialog();
					
				}
				
			  })
		  }
		});
	  }

	   // ======================================================
  // * Function to check the validations on the change of password
  // ======================================================

	  check(){
		if(this.new_password == this.confirm_password){
		  this.x = "Password matched successfully";
		  this.passmatch = 1;
		}
		else{
		  this.x = "Those passwords didn't match. Try again";
		  this.passmatch = 0;
		}
	  }

	  passwordformcontrol= new FormControl('',[
		Validators.required,
		Validators.minLength(6),
		Validators.maxLength(12),
		
		Validators.pattern(/(?=^.{8,16}$)(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&amp;*()_+}{&quot;:;'?/&gt;.&lt;,])(?!.*\s).*$/)
	  ])
	  passwordmatcher= new passworderrormatcher();
}


