import { Component, OnInit, Inject, AfterViewInit } from '@angular/core';
import { companyinfo, ticdetails, ticketDetails, userMessage } from 'src/app/user.model';
import { AuthenticateService } from 'src/app/authenticate.service';
import { MatDialog, MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { AddticketComponent } from '../addticket/addticket.component';
import { Router } from '@angular/router';
import { WebsocketService } from 'src/app/websocket.service';
import { HttpClient, HttpBackend, HttpEventType, HttpResponse } from '@angular/common/http';
import { UploadService } from 'src/app/services/upload.service';

import { LoaderserviceService } from 'src/app/loaderservice.service';

@Component({
	selector: 'app-helpdesk',
	templateUrl: './helpdesk.component.html',
	styleUrls: ['./helpdesk.component.scss']
})
export class HelpdeskComponent implements OnInit {
	private httvClient: HttpClient;
	ticketId: string;

  // ======================================================
  // * Web Socket Service for chat application
  // ======================================================

	constructor(private helpdesk: AuthenticateService, private dialog: MatDialog,private websocketservice: WebsocketService,loader: HttpBackend) {
		this.httvClient = new HttpClient(loader);
		this.websocketservice.newMessageReceived().subscribe(data => {
			console.log(data);
		  });
	 }

  // ======================================================
  // *Open the add ticket dialog box, which is a component
  // ======================================================


	openDialog() {
		const dialogConfig = new MatDialogConfig();
		dialogConfig.autoFocus = true;
		dialogConfig.width = "40%";
		this.dialog.open(AddticketComponent, dialogConfig);
	}


  // ======================================================
  // * Display the tickets on the loading of page 
  // ======================================================

	public ticinfo: Array<ticdetails>;

	ngOnInit() {
		this.helpdesk.getticinfo().subscribe(res => {
			this.ticinfo = res;
			console.log(res);
		});
		this.websocketservice.sendMessage({user: "sid", message: "testing"});
	}
	displayedColumns: string[] = ['timestamp', 'category', 'subject', 'status', '_id'];


  // ======================================================
  // *Open the helpdesk chat dialog, which is declared below in the code
  // ======================================================

	openHelpdeskchat(_id): void {
		
		this.helpdesk.getticinfo().subscribe(res => {
			this.ticinfo = res;
			console.log(res);
		});
		const dialogRef = this.dialog.open(HelpdeskchatDialog, {
			width: '500px',
			// height: '500px',
			data: { ticId: _id }
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log('The dialog was closed' + result);
			this.helpdesk.getticinfo().subscribe(res => {
				this.ticinfo = res;
				
				console.log(res);
			});
		});
	}
}


  // ======================================================
  // * Helpdesk Chat Dialog
  // ======================================================



@Component({
	selector: 'dialog-overview-example-dialog',
	templateUrl: 'helpdeskchat.html',
})
export class HelpdeskchatDialog implements OnInit, AfterViewInit {
	private httvClient: HttpClient;
	container: HTMLElement; 
	selectedFiles: FileList;
  	currentFileUpload: File;
  	progress: { percentage: number } = { percentage: 0 };
	constructor(
		public dialogRef: MatDialogRef<HelpdeskchatDialog>,loader: HttpBackend,
		@Inject(MAT_DIALOG_DATA) public data: { ticId: string },private websocketservice: WebsocketService, private helpdeskchat: AuthenticateService, private router: Router,private uploadService: UploadService) {
			this.httvClient = new HttpClient(loader);
			this.websocketservice.newMessageReceived().subscribe(data => {
				console.log(data);
				var newMessage={
					message: data.message,
					flag: data.flag,
					dateTime: data.dateTime
				};
				this.allMessageArray.push(newMessage);
			  });
			  
		 }
		 
	_id: string;
	message: string;
	flag = "1";
	usermessage: userMessage;
	sendMessage() {
		this.usermessage = {
			message: this.message,
			_id: this.ticketmessagedetails._id,
			flag: this.flag
		}
		this.websocketservice.sendMessage({flag: this.flag, message: this.message,room: this.data.ticId});
		this.helpdeskchat.sendUserMessage(this.usermessage).subscribe(()=>{
			this.message=null;
		});
	}
	ticketmessagedetails: ticketDetails;
	allMessageArray: Array<{message: string, flag: string, dateTime: Date}>=[];
	ngOnInit() {
		this.helpdeskchat.helpdeskChat(this.data.ticId, JSON.parse(window.localStorage.getItem('user')).email, null).subscribe(res => {
			this.ticketmessagedetails = res;
			this.allMessageArray= res.allMessage;
			console.log(res);
			
		})
		
		this.websocketservice.joinRoom({room: this.data.ticId});
		// setInterval(() => {
		//   this.helpdeskchat.helpdeskChat(this.data.ticId).subscribe(res=>{
		//     this.ticketmessagedetails = res;
		//     console.log(res);
		//   })
		//   }, 5000);
	}
	selectFile(event) {
		this.selectedFiles = event.target.files;
	  }
	
	  upload() {
		this.progress.percentage = 0;
	
		this.currentFileUpload = this.selectedFiles.item(0);
		this.uploadService.pushFileToStorage(this.currentFileUpload).subscribe(event => {
		  if (event.type === HttpEventType.UploadProgress) {
			this.progress.percentage = Math.round(100 * event.loaded / event.total);
		  } else if (event instanceof HttpResponse) {
			console.log('File is completely uploaded!');
		  }
		});
	
		this.selectedFiles = undefined;
	  }
	ngAfterViewInit() {         
		this.container = document.getElementById("chat-content");           
		this.container.scrollTop = this.container.scrollHeight;     
	  }
	// intervalfunction() {

	// }
	onNoClick(): void {
		this.dialogRef.close();
		
	}

}
