import { Component, OnInit, Inject } from '@angular/core';
import { adminTickets } from 'src/app/admin.model';
import { AdminserviceService } from 'src/app/adminservice.service';
import { MAT_DIALOG_DATA, MatDialogRef, MatDialog } from '@angular/material';
import { ticketDetails, userMessage, message } from 'src/app/user.model';
import { Router } from '@angular/router';
import { WebsocketService } from 'src/app/websocket.service';

@Component({
  selector: 'app-tickets',
  templateUrl: './tickets.component.html',
  styleUrls: ['./tickets.component.scss']
})
export class TicketsComponent implements OnInit {

  constructor( private usertickets: AdminserviceService, private dialog: MatDialog) { }
  adminTickets: Array<adminTickets>
  ngOnInit() {
    this.usertickets.getAdminTickets().subscribe(res=> 
      {
        this.adminTickets = res;
        console.log(res);
      });
  }
  openAdminhelpdeskchat(_id): void {
		const dialogRef = this.dialog.open(AdminhelpdeskchatDialog, {
      width: '500px',
      height: '500px',
		  data: {ticId: _id}
		});
	
		dialogRef.afterClosed().subscribe(result => {
		  console.log('The dialog was closed'+result);
		  this.usertickets.getAdminTickets().subscribe(res=> 
        {
          this.adminTickets = res;
          console.log(res);
        });
		});
	  }
}


@Component({
	selector: 'dialog-overview-example-dialog',
	templateUrl: 'adminhelpdeskchat.html',
  })
  export class AdminhelpdeskchatDialog implements OnInit {
  adminmessagedetails: ticketDetails;
	allMessageArray: Array<{message: string, flag: string, dateTime: Date}>=[];
	constructor(
	  public dialogRef: MatDialogRef<AdminhelpdeskchatDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {ticId: string},private websocketservice: WebsocketService, private adminhelpdeskchat: AdminserviceService, private router: Router) {
      this.websocketservice.newMessageReceived().subscribe(data => {
				console.log(data);
				var newMessage={
					message: data.message,
					flag: data.flag,
					dateTime: data.dateTime
				};
				this.allMessageArray.push(newMessage);
			  });
    }
    _id: string;
    message: string;
    flag = "0";
    adminmessage: userMessage;
    sendMessage(){
      this.adminmessage={
        message: this.message,
        _id: this.adminmessagedetails._id,
        flag: this.flag
      }
      this.websocketservice.sendMessage({flag: this.flag, message: this.message,room: this.data.ticId});
     this.adminhelpdeskchat.sendAdminMessage(this.adminmessage).subscribe(res=>{
       this.message=null;
     });


    }
    ngOnInit(){
      this.adminhelpdeskchat.getAdminChat(this.data.ticId,null,JSON.parse(window.localStorage.getItem('admin')).adminId).subscribe(res=>{
        this.adminmessagedetails = res;
        this.allMessageArray= res.allMessage;
      console.log(res);
      
      })
      this.websocketservice.joinRoom({room: this.data.ticId});
      }
      closeticketreturn: message
      closeTicket(){
        console.log(this.adminmessagedetails._id)
        this.adminhelpdeskchat.closehelpticket(this.adminmessagedetails._id).subscribe(res=>{
          this.closeticketreturn = res;
          console.log(res);
          this.onNoClick();
        });
          
      }
      onNoClick(): void {
        this.dialogRef.close();
        this.router.navigateByUrl('/admin/dashboard', {skipLocationChange: true}).then(()=>
            this.router.navigate(["/admin/tickets"]));
      }
    }
  