import { Component, OnInit, Inject } from '@angular/core';
import { NavigationExtras, Router, ActivatedRoute, NavigationStart,NavigationEnd, Event} from '@angular/router';
import { message, DialogData } from 'src/app/user.model';
import { AuthGuard } from 'src/app/auth.guard';
import { AuthenticateService } from 'src/app/authenticate.service';
import { AdminserviceService } from 'src/app/adminservice.service';
import { checkStageActive, displayStageDetails } from 'src/app/admin.model';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material';

@Component({
  selector: 'app-stostageselect',
  templateUrl: './stostageselect.component.html',
  styleUrls: ['./stostageselect.component.scss']
})
export class StostageselectComponent implements OnInit {
  stage: string;
  
  showLoadingIndicator = true;
  constructor(private router: Router, private selectstage: AdminserviceService, private dialog: MatDialog) { 
    this.router.events.subscribe((routerEvent: Event)=>{
      if(routerEvent instanceof NavigationStart){
        this.showLoadingIndicator = true;
      }
      if(routerEvent instanceof NavigationEnd){
        this.showLoadingIndicator = false;
      }
    });
  }
  selectStage(selectedStage){ 
    
    console.log(selectedStage);
    let navigationExtras:  NavigationExtras= {
      queryParams: { 'stage':  selectedStage}
    };
    
    setTimeout(()=>{this.router.navigate(['/admin/stostages'], navigationExtras);},0);
    this.showLoadingIndicator = true;
  }
  activateStageResponse: message;
  activeStage(selectedStage){
    this.openDialog(selectedStage)
    console.log(selectedStage);
     
  }
  pauseStageResponse: message;
  pauseStage(stagepause){
    this.openPauseDialog(stagepause);
    console.log(stagepause);
    // this.selectstage.pausestage(stagepause).subscribe(res=>{
    //   this.pauseStageResponse = res;
    //   this.selectstage.checkActiveStage().subscribe(res=>{
    //     this.activestageresponse = res;
    //     console.log(res);
    //   })
    //   console.log(res);
    // })
  }
  activestageresponse: checkStageActive;
  displaystagedetails: Array<displayStageDetails>;
  ngOnInit() {
    this.selectstage.checkActiveStage().subscribe(res=>{
      this.activestageresponse = res;
      console.log(res);
    })
    this.selectstage.displayStageDetails().subscribe(res=>{
      this.displaystagedetails = res;
      console.log(res);
    })
  }
  openDialog(selectStage): void {
    console.log(selectStage);    
    const dialogRef = this.dialog.open(stageconfirmationDialog, {
      width: '250px',
      data: {selectedstage: selectStage}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.selectstage.checkActiveStage().subscribe(res=>{
        this.activestageresponse = res;
        console.log(res);
      })
      this.selectstage.displayStageDetails().subscribe(res=>{
        this.displaystagedetails = res;
        console.log(res);
      })
    });
  }
  openPauseDialog(pauseStage): void {
    console.log(pauseStage);    
    const dialogRef = this.dialog.open(pauseconfirmationDialog, {
      width: '250px',
      data: {pausestage: pauseStage}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.selectstage.checkActiveStage().subscribe(res=>{
        this.activestageresponse = res;
        console.log(res);
      })
      this.selectstage.displayStageDetails().subscribe(res=>{
        this.displaystagedetails = res;
        console.log(res);
      })
    });
  }
}

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'stageconfirmation.html',
})
export class stageconfirmationDialog {

  constructor(
    public dialogRef: MatDialogRef<stageconfirmationDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {selectedstage: string}, private selectstage: AdminserviceService, private router: Router) {}
    activateStageResponse: message;
    activestageresponse: checkStageActive;
    confirmStageChange(stage){
      this.selectstage.activatestage(stage).subscribe(res=>{
        this.activateStageResponse = res;
        this.onNoClick();
        console.log(res);
        
      })
      
    }
  onNoClick(): void {
    this.dialogRef.close();
    
  }
 

}
@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'pauseconfirmation.html',
})
export class pauseconfirmationDialog {

  constructor(
    public dialogRef: MatDialogRef<pauseconfirmationDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {pausestage: string},private pausestage: AdminserviceService) {}
    pauseStageResponse: message;
    confirmStagePause(pausestage){
      this.pausestage.pausestage(pausestage).subscribe(res=>{
        this.pauseStageResponse = res;
        this.onNoClick();
        console.log(res);
      })
    }

  onNoClick(): void {
    this.dialogRef.close();
  }
 

}