import { Component, OnInit } from '@angular/core';
import { AuthenticateService } from 'src/app/authenticate.service';
import { otpdetails } from 'src/app/user.model';

@Component({
  selector: 'app-forgotpassword',
  templateUrl: './forgotpassword.component.html',
  styleUrls: ['./forgotpassword.component.scss']
})
export class ForgotpasswordComponent implements OnInit {
  email:string;
  otp:string;
  returnmessage: {flag:string};
  otpresponse: {flag:string};
  otpverifydetails: otpdetails;


  constructor(private pass: AuthenticateService) { }

  ngOnInit() {
  }

  resetpass(){
    this.pass.changepass(this.email).subscribe(res=>{
      this.returnmessage=res;
    })
  }

 

}
