import { Component, OnInit,Inject } from '@angular/core';
import { AuthenticateService } from '../authenticate.service';
import { User , returnmsg, Verificationcode, msg, DialogData} from '../user.model';
import { CodeNode } from 'source-list-map';
import { Router, NavigationExtras } from '@angular/router';
import {FormControl, FormGroupDirective, NgForm, Validators,ReactiveFormsModule} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';
import { MatDialog, MatDialogConfig, MatDialogRef,MAT_DIALOG_DATA } from '@angular/material';
import { EmailverifyComponent } from './emailverify/emailverify.component';
import { AddticketComponent } from '../layout/addticket/addticket.component';
import { ToastrService } from 'ngx-toastr';



export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

export class passworderrormatcher implements ErrorStateMatcher{
  isErrorState(control: FormControl| null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted= form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted))
  }
}

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss'],
  providers: [AuthenticateService]
})


export class SignupComponent implements OnInit {

  newuser: User;
  verify: Verificationcode={
      email: ' ',
      code: ' '
  }
  public x = " ";
  public passmatch:number = 0;
  public auth = "0";
  public loadform = '1';
  public message:msg = {err: null,msg: null,user: null,email: null};
  name: string;
  email: string;
  password: string;
  referral: string;
  confirm_password: string;
  code: string;
  lastName: string;
  constructor(private _signUpservice: AuthenticateService, private router: Router, private dialog: MatDialog,  private toastr:ToastrService) { }
  ischecked: boolean= false;

  
  check(){
    if(this.password == this.confirm_password){
      this.x = "Password matched successfully";
      this.passmatch = 1;
    }
    else{
      this.x = "Those passwords didn't match. Try again";
      this.passmatch = 0;
    }
    if((this.name != null)&&(this.email != null)){
      this.auth = '1';
    }

  }
 
  onSubmit(){
    this.loadform = '0';
    this.newuser={
      name: this.name,
      lastName: this.lastName,
      email: this.email,
      referral: this.referral,
      password: this.password,
      confirm_password: this.confirm_password
    };

  this._signUpservice.postSignupData(this.newuser)
  .subscribe(res=>{
    this.message = res;
    this.ischecked=false;
    if(this.message.msg=='1'){
      this.openDialog()
    }
  });
  }
  keyDownFunction(event) {
    if(event.keyCode == 13) {
      this.onSubmit ();
    }
  }
  verifycode(){
    this.verify = {
      email: this.email,
      code: this.code
    };
    console.log(this.verify);
    this._signUpservice.Verifyuser(this.verify)
  .subscribe(res=> {
    this.message = res;
    let navigationExtras: NavigationExtras = {
      queryParams: { 'email':  this.email}
    };
    this.ischecked=true;
    if(res.msg == "2"){
      this.router.navigate(['signin']);
      this.openToastr();
    }
    if(res.msg=="-2")
    {
      this.openDialog();
    }
  });
  }
  openToastr(){
    this.toastr.success('Sign Up Successful');
  }
  ngOnInit() {
    this.loadform = '1';
    window.localStorage.clear();
    this.name = "";
    this.email= "";
    this.password = "";
  }

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)
  ]);
  matcher = new MyErrorStateMatcher();

  passwordformcontrol= new FormControl('',[
    Validators.required,
    Validators.minLength(6),
    Validators.maxLength(12),
    
    Validators.pattern(/(?=^.{8,16}$)(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&amp;*()_+}{&quot;:;'?/&gt;.&lt;,])(?!.*\s).*$/)
  ])
  passwordmatcher= new passworderrormatcher();

  togglechecked(){
    if(this.ischecked==false)
    {
      this.ischecked=true;
    }
    else 
    {
      this.ischecked=false;
    }
  }


  openDialog(): void {
    const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
      width: '620px',
      height: 'auto',
      disableClose: true,
      data: {code: this.code,email: this.email, msg: this.message.msg}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed'+result);
      this.code = result;
      this.verifycode();
    });
  }

}



@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'otp-dialog.html',
})
export class DialogOverviewExampleDialog {

  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {code: string,email: string,msg: string}) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}