
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import {FormControl, FormGroupDirective, NgForm, Validators,ReactiveFormsModule} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';
import { AdminserviceService } from '../adminservice.service';
import { returnmessage, admin } from '../admin.model';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

export class passworderrormatcher implements ErrorStateMatcher{
  isErrorState(control: FormControl| null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted= form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted))
  }
}
@Component({
  selector: 'app-admsignin',
  templateUrl: './admsignin.component.html',
  styleUrls: ['./admsignin.component.scss']
})

export class AdmsigninComponent implements OnInit {
  
  companyemail:string;
  password:string;
  message:returnmessage;
  admin: admin;
  logindetails: {email:string,password:string};

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)
  ]);
  matcher = new MyErrorStateMatcher();

  passwordformcontrol= new FormControl('',[
    Validators.required,
    Validators.minLength(6),
    Validators.maxLength(12),
    
    Validators.pattern(/(?=^.{8,16}$)(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&amp;*()_+}{&quot;:;'?/&gt;.&lt;,])(?!.*\s).*$/)
  ])
  passwordmatcher= new passworderrormatcher();

  constructor(private adminservice: AdminserviceService, private router:Router) { }

  loginSubmit(){
    this.logindetails={
      email: this.companyemail,
      password: this.password
    }
    this.adminservice.loginadmin(this.logindetails).subscribe(res=>{
      this.message=res;
      if(res.msg == '1'){
        this.admin= {
          adminName: res.adminName,
          adminId: res.adminId
        }
        window.localStorage.clear();
        window.localStorage.setItem('admin',JSON.stringify(this.admin));
        this.router.navigate(['admin'])
      console.log(res);
      console.log(this.message);
    }else{
      this.router.navigate(['adminsignin'])
    }});
  }

  ngOnInit() {
  }

}
