import { Component, OnInit } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material';
import { FormControl, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { receivingWalletAddress, message, getReceivingAddress } from 'src/app/user.model';
import { AuthenticateService } from 'src/app/authenticate.service';
export class ethErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
export class passworderrormatcher implements ErrorStateMatcher{
	isErrorState(control: FormControl| null, form: FormGroupDirective | NgForm | null): boolean {
	  const isSubmitted= form && form.submitted;
	  return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted))
	}
  }
  
@Component({
  selector: 'app-recivingwalletaddress',
  templateUrl: './recivingwalletaddress.component.html',
  styleUrls: ['./recivingwalletaddress.component.scss']
})
export class RecivingwalletaddressComponent implements OnInit {
  wallet_address: string;
  confirm_wallet_address: string;
  receivingaddress: string;
  confirmreceivingaddress: string;
  public passmessage = " ";
  public addressmatch:number = 0;
  public toggle:string = '0';
  ethFormControl = new FormControl('', [
		Validators.pattern(/^0x[a-fA-F0-9]{40}$/)
	]);
  ethmatcher = new ethErrorStateMatcher();
  check(){
		if(this.wallet_address == this.confirm_wallet_address){
		  this.passmessage = "Password matched successfully";
		  this.addressmatch = 1;
		}
		else{
		  this.passmessage = "Those passwords didn't match. Try again";
		  this.addressmatch = 0;
		}
	  }

	  passwordformcontrol= new FormControl('',[
		Validators.required,
		Validators.minLength(6),
		Validators.maxLength(12),
		
		Validators.pattern(/(?=^.{8,16}$)(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&amp;*()_+}{&quot;:;'?/&gt;.&lt;,])(?!.*\s).*$/)
	  ])
	  passwordmatcher= new passworderrormatcher();
  constructor(private recievewallet: AuthenticateService) { }
  recivingwalletaddress: receivingWalletAddress;
  resultreceivingaddress:message;
  submitWalletAddress(){
    this.recivingwalletaddress = {
      receivingaddress: this.receivingaddress,
      confirmreceivingaddress: this.confirmreceivingaddress,
      email: JSON.parse(window.localStorage.getItem('user')).email
    }
    console.log(this.recivingwalletaddress);
    this.recievewallet.sendReceivingAddress(this.recivingwalletaddress).subscribe(res=>{
      this.resultreceivingaddress = res;
      console.log(res);
      
      if(res.msg == '1'){
        this.toggle = "1";
        this.recievewallet.getReceivingAddress().subscribe(res=>{
          this.displayaddress = res;
        })
      }
    });
  }
  displayaddress: getReceivingAddress
  ngOnInit() {
    this.recievewallet.getReceivingAddress().subscribe(res=>{
      this.displayaddress = res;
    })
  }

}
