import { Component, OnInit, Inject } from '@angular/core';
import { AuthenticateService } from 'src/app/authenticate.service';
import { transactdata, transactiondetails, message, transactionHelpTicket, transactionHelpMessage } from 'src/app/user.model';
import { MatExpansionPanel,PageEvent } from '@angular/material';
import { Router } from '@angular/router';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

export interface DialogData {
  // animal: string;
  // name: string;
  info: transactdata;
  statusIcon: string;
}
@Component({
  selector: 'app-transaction',
  templateUrl: './transaction.component.html',
  styleUrls: ['./transaction.component.scss'],
  viewProviders: [MatExpansionPanel]
})
export class TransactionComponent implements OnInit {
  transactinfo: Array<transactdata>;
  txd:string;
  verifytrans: transactiondetails;
  currenttransaction: transactdata;
  testingreturn : message;
  testingbtcreturn : message
 
  constructor(private transactiondata: AuthenticateService, private router: Router, public dialog: MatDialog) { }
  tesingFunction(){
    this.transactiondata.testroute().subscribe(res=>{
      this.testingreturn = res;
    })
    
  }
  tesingBTCFunction(){
    this.transactiondata.testbtcroute().subscribe(res=>{
      this.testingbtcreturn = res;
    })
  }
  ngOnInit() {
    this.transactiondata.gettransact().subscribe(res=>{
      this.transactinfo = res;
      console.log(res); 
      
    })
  }

  displayedColumns: string[] = ['statusIcon','orderId', 'claimAmt', 'currencyAmt','fromAddress', 'txnStatus','_id'];
  length = 100;
  pageSize = 5;
  pageSizeOptions = [1,2,5,10];
  verifytransaction(id:string){
    console.log(id);
    console.log(this.txd);
    this.verifytrans={
      orderid: id,
      txd: this.txd
    }
    this.transactiondata.verifytransaction(this.verifytrans).subscribe(res=>{
      console.log("time to refresh");
      
      this.router.navigateByUrl('/viewprof',{skipLocationChange: true}).then(()=>
        this.router.navigate(["/transaction"]));
    });
    setTimeout(()=>{this.router.navigateByUrl('/viewprof',{skipLocationChange: true}).then(()=>
    this.router.navigate(["/transaction"]));},3000)
  }

  openDialog(id): void {
    console.log(id);
    this.transactiondata.orderinfo(id).subscribe(res=>{
      this.currenttransaction=res;
      console.log(this.currenttransaction);
      let dialogRef = this.dialog.open(transactionViewDialog, {
        width: '600px',
        minHeight: '500px',
        disableClose: true,
       data: {info: this.currenttransaction}
      });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        
        //this.animal = result;
      });
    })
  }

  openTicketDialog(id, fromAdd, claimamount): void {
    console.log(id);
      let dialogRef = this.dialog.open(expiredTxnTicketDialog, {
        width: '600px',
        minHeight: '500px',
        disableClose: true,
       data: {id: id, fromaddress: fromAdd, claimAmt: claimamount}
      });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        
        //this.animal = result;
      });
    
  }
}

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'transactionViewDialog.html',
})
export class transactionViewDialog {

  constructor(
    public dialogRef: MatDialogRef<transactionViewDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }
 

}
@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'expiredTxnTicketDialog.html',
})
export class expiredTxnTicketDialog {
  transactionhelpticket: transactionHelpTicket;
  message: transactionHelpMessage;
  transactionId: string;
  // claimAmount: string;
  flag: string = '1';
  companyId: string = JSON.parse(window.localStorage.getItem('company')).companyId;
  query: string = 'payment';
  subject: string='Transaction Review'

  constructor(
    public dialogRef: MatDialogRef<expiredTxnTicketDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {id: string, claimAmt: string, fromaddress: string}, private transactionhelptix: AuthenticateService) {}
    returnhelpticket: message;
    
  submitTicket(){
    console.log(this.data.id);
    console.log(this.data.claimAmt);
    console.log(this.data.fromaddress);
    this.message={
      trsansactionId: this.data.id,
      claimAmount: this.data.claimAmt,
      fromaddress: this.data.fromaddress,
      transactionHash: this.transactionId
    }
    this.transactionhelpticket={
      email: JSON.parse(window.localStorage.getItem('user')).email,
      companyId: this.companyId,
      query: this.query,
      subject: this.subject,
      message: ' My transaction details are:  ' +'============================ '+'Order Id:  '  +this.message.trsansactionId + ' '+'============================ '+' Claim Amount: '+ this.message.claimAmount + ' '+'============================ ' + ' From Address: '+ this.message.fromaddress+ ' '+'============================ ' + ' Transaction ID: ' + this.message.transactionHash,
      flag: this.flag
    }
    this.transactionhelptix.submittransactionhelpticket(this.transactionhelpticket).subscribe(res=>{
      this.returnhelpticket = res;
      this.onNoClick();
    })
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
 

}

