  // ======================================================
  // *set tokenvalue based on selected currency and call other functions to set associated variables
  // ======================================================
  setTokenValue(){
    if(this.currentCurrency == 'eth')
    this.tokenValue= this.companyIdentity.tokenValuePerETH;
    if(this.currentCurrency == 'btc')
    this.tokenValue= this.companyIdentity.tokenValuePerBTC;
    this.calculateBaseTokenValue();
    this.calculateSaleBonus();
    this.calculateAmountBonus();
    this.calculatePromoBonus();
    this.checkMinMax();
  }

  // ======================================================
  // *change the selected currency and call setTokenValue function to change other variables accordingly
  // ======================================================
  setcurrency(currentCurrency: string){
    this.currentCurrency=currentCurrency;
    if(this.currentCurrency=='eth')
    this.iconName='ethereum';
    else
    this.iconName='btc';
    this.setTokenValue();
  }

  // ======================================================
  // *check for minimum and maximum transaction allowed
  // ======================================================
  checkMinMax(){
    if(!Number(this.claimAmt))
    {
      this.minError=true;
      this.maxError=true;
      return;
    }
    if((this.claimAmt*this.tokenValue)<this.companyIdentity.minTrans){
      this.minError=true;
      this.maxError=false;
    }
    else if((this.claimAmt*this.tokenValue)>=this.companyIdentity.minTrans && (this.claimAmt*this.tokenValue)<=this.companyIdentity.maxTrans){
      this.minError=false;
      this.maxError=false;
    }
    else if((this.claimAmt*this.tokenValue)>this.companyIdentity.maxTrans){
      this.maxError=true;
      this.minError=false;  
    }
    else
    {
      this.minError=true;
      this.maxError=true;
    }
  }

  // ======================================================
  // *calculate sale Bonus for the entered claim amount
  // ======================================================
  calculateSaleBonus(){
    if(!Number(this.claimAmt))
    {
      this.saleBonus=0;
      return;
    }
    this.saleBonus= (this.claimAmt*this.tokenValue)*((this.companyIdentity.baseBonus)/100)
  }

  // ======================================================
  // *calculate Bonus earned based on amount
  // ======================================================
  calculateAmountBonus(){
    if(!Number(this.claimAmt))
    {
      this.amountBonus= 0;
      return;
    }
    if(this.claimAmt*this.tokenValue<this.companyIdentity.extraBonus[0].amount)
    {
      this.amountBonus=0;
    }
    this.companyIdentity.extraBonus.forEach(extra => {
      if(this.claimAmt*this.tokenValue>extra.amount){
        this.amountBonus=(this.claimAmt*this.tokenValue)*(extra.bonus/100);
      }
    });
  }

  // ======================================================
  // *calculate Bonus earned based on promo code
  // ====================================================== 
  calculatePromoBonus(){
    if(!Number(this.claimAmt))
    {
      this.promoBonus= 0;
      return;
    }
    this.promoBonus=(this.claimAmt*this.tokenValue)*(this.promoBonusPercent/100);
  }

  // ======================================================
  // *calculate no of tokens for the entered claim amount
  // ======================================================
  calculateBaseTokenValue(){
    if(!Number(this.claimAmt))
    {
      this.baseTokenValue=0;
      return;
    }
    this.baseTokenValue= (this.claimAmt*this.tokenValue)
  }

  setBaseBonusWidth(){
    if(this.companyIdentity.baseBonus==0)
    {
      this.baseBonusBarWidth=0;
      this.extraBonusBarWidth=100;
    }
    else if(this.companyIdentity.extraBonus.length<1)
    {
      this.baseBonusBarWidth=100;
      this.extraBonusBarWidth=0;
    }
    else{
      this.baseBonusBarWidth=20;
      this.extraBonusBarWidth=80;
    }
  }

  calculateCurrentExtraBonus(){
    if(!Number(this.claimAmt))
    {
      this.currentExtraBonus= 0;
      return;
    }
    if(this.claimAmt*this.tokenValue<this.companyIdentity.extraBonus[0].amount){
      this.currentExtraBonus=this.companyIdentity.extraBonus[0].amount;
    }
    for(var i=0;i<this.companyIdentity.extraBonus.length-1;i++)
    {
      if(this.claimAmt*this.tokenValue>this.companyIdentity.extraBonus[i].amount)
      {
        this.currentExtraBonus=this.companyIdentity.extraBonus[i+1].amount;
      }
    }
  }

  // setProgressBar(extraBonusAmount){
  //   console.log(extraBonusAmount);
  //   let elem = document.getElementById("amountProgressBar"); 
  //   if(!Number(this.claimAmt))
