@Component({
	selector: 'makepay-dialog',
	templateUrl: 'makepay.html',
})
export class MakepayDialog implements OnInit {
	addressmatch: boolean;
	ischecked: boolean = false;

	ethFormControl = new FormControl('', [
		Validators.pattern(/^0x[a-fA-F0-9]{40}$/)
	]);
	ethmatcher = new ethErrorStateMatcher();

	btcFormControl = new FormControl('', [
		Validators.pattern(/^[13][a-km-zA-HJ-NP-Z1-9]{25,34}$/)
	]);
	btcmatcher = new btcErrorStateMatcher();

	constructor(
		public dialogRef: MatDialogRef<MakepayDialog>,
		@Inject(MAT_DIALOG_DATA) public data: { btcrefundaddress: string, ethrefundaddress: string, ethaddress: string, btcaddress: string, companyaddress: addressinfo, currency: string, address: string, refundaddress: string }) { }

	onNoClick(): void {
		this.dialogRef.close();
	}

	ngOnInit() {

	}

	setrefundaddress() {
		// console.log("setting refund address");

		if (this.data.currency == "eth" && !this.data.address) {
			this.data.address = this.data.ethaddress;
		}
		else if (this.data.currency == "btc" && !this.data.address) {
			this.data.address = this.data.btcaddress;
		}
		else (this.data.address)
		{
			this.data.refundaddress = this.data.address;
		}
	}

	confirmaddress() {
		// console.log("from confirm address");
		if (this.data.currency == "eth" && !this.data.address) {
			this.data.address = this.data.ethaddress;
		}
		if (this.data.currency == "btc" && !this.data.address) {
			this.data.address = this.data.btcaddress;
		}
		if (this.ischecked == false) {
			this.ischecked = true;
		}
		else {
			this.ischecked = false;
		}
	}

}


