import { Component, OnInit, Inject } from '@angular/core';
import { adminTransactionList, transactdata, viewUserDetails } from 'src/app/admin.model';
import { AdminserviceService } from 'src/app/adminservice.service';
import { MatExpansionPanel, MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { expiredTxnTicketDialog } from 'src/app/layout/transaction/transaction.component';
import { DialogData, message } from 'src/app/user.model';

export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}
const ELEMENT_DATA: PeriodicElement[] = [
  {position: 1, name: 'Hydrogen', weight: 1.0079, symbol: 'H'},
  {position: 2, name: 'Helium', weight: 4.0026, symbol: 'He'},
  {position: 3, name: 'Lithium', weight: 6.941, symbol: 'Li'},
  {position: 4, name: 'Beryllium', weight: 9.0122, symbol: 'Be'},
  {position: 5, name: 'Boron', weight: 10.811, symbol: 'B'},
  {position: 6, name: 'Carbon', weight: 12.0107, symbol: 'C'},
  {position: 7, name: 'Nitrogen', weight: 14.0067, symbol: 'N'},
  {position: 8, name: 'Oxygen', weight: 15.9994, symbol: 'O'},
  {position: 9, name: 'Fluorine', weight: 18.9984, symbol: 'F'},
  {position: 10, name: 'Neon', weight: 20.1797, symbol: 'Ne'},
];
@Component({
  selector: 'app-admtransactions',
  templateUrl: './admtransactions.component.html',
  styleUrls: ['./admtransactions.component.scss'],
  // viewProviders: [MatExpansionPanel]
})


export class AdmtransactionsComponent implements OnInit {

  constructor(private admintransaction: AdminserviceService, public dialog: MatDialog) { }
  adminTransactionList: Array<adminTransactionList>;
  currentTransactionDetails: transactdata;
  sendId(_id){
    this.admintransaction.sendTransactionId(_id).subscribe(res=> {
      this.currentTransactionDetails = res;
      console.log(res);
    })
  }
  ngOnInit() {
    this.admintransaction.getAdminTransactionList().subscribe(res=> 
      {
        this.adminTransactionList = res;
        console.log(res);
      });
  }
  dataSource = ELEMENT_DATA;
  // displayedColumns: string[] = ['userEmail'];
  
  displayedColumns: string[] = ['userEmail', 'claimAmt', 'currency', 'orderId', 'currencyAmt', 'txnStatus', 'fromAddress', 'timeStamp', 'getDetails'];
  // displayedColumns: string[] = ['statusIcon','userEmail','orderId', 'claimAmt', 'currency','currencyAmt','fromAddress', 'txnStatus','timeStamp'];

  openUserDetailsViewDialog(_id): void {
    console.log(_id);
      let dialogRef = this.dialog.open(userDetailsViewDialog, {
        width: '600px',
        minHeight: '500px',
       data: {_id: _id}
      });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        
        //this.animal = result;
      });
    
  }
}
@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'viewUserDetailsDialog.html',
})
export class userDetailsViewDialog implements OnInit {
  txnHash: string;
  currencyAmt: number;
  getApprovalDetails: viewUserDetails;
  acceptStatus: string = "success";
  rejectStatus: string = "expire";
  constructor(
    public dialogRef: MatDialogRef<userDetailsViewDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {_id: string}, private userdetailsview: AdminserviceService) {}
    currentTransactionDetails: transactdata;
    ngOnInit(){
      this.userdetailsview.sendTransactionId(this.data._id).subscribe(res=> {
        this.currentTransactionDetails = res;
        console.log(res);
      })
    }
    viewTransactionsReturn: message;
    transactionacceptstatus(){
      this.getApprovalDetails={
        _id: this.data._id,
        currencyAmt: this.currencyAmt,
        txnHash: this.txnHash,
        status: this.acceptStatus
      }
      console.log(this.getApprovalDetails);
      this.userdetailsview.decidetransactionstatus(this.getApprovalDetails).subscribe(res=>{
        this.viewTransactionsReturn = res;
      })
     
    }
    transactionrejectstatus(){
      this.getApprovalDetails={
        _id: this.data._id,
        currencyAmt: this.currencyAmt,
        txnHash: this.txnHash,
        status: this.rejectStatus
      }
      console.log(this.getApprovalDetails);
      this.userdetailsview.decidetransactionstatus(this.getApprovalDetails).subscribe(res=>{
        this.viewTransactionsReturn = res;
      })
     
    }
  onNoClick(): void {
    this.dialogRef.close();
  }
 

}
