import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { map }                from 'rxjs/operators';
import { Mobileotp, otpReturns } from 'src/app/user.model';
import { AuthenticateService } from 'src/app/authenticate.service';
@Component({
  selector: 'app-mobileotp',
  templateUrl: './mobileotp.component.html',
  styleUrls: ['./mobileotp.component.scss']
})
export class MobileotpComponent implements OnInit {
  requestId: string;
  verify: Mobileotp;
  code: string;
  email: string;
  number: string;
  public otpReturn: otpReturns;
  constructor(private otpverify: AuthenticateService, private route: ActivatedRoute, private router: Router) { }
  verifyotp(){
    this.route
    .queryParamMap
    .pipe(map(params => params.get('requestId')))
    .subscribe(
      res=> this.requestId = res
    )
    this.route
    .queryParamMap
    .pipe(map(params => params.get('email')))
    .subscribe(
      res=> this.email = res
    )
    this.route
    .queryParamMap
    .pipe(map(params => params.get('number')))
    .subscribe(
      res=> this.number = res
    )
    this.verify = {
      code: this.code,
      requestId: this.requestId,
      email: this.email,
      number: this.number
    };
    this.otpverify.verifyOtp(this.verify).subscribe(res=> {
      this.otpReturn = res;
      if(res.message == "1"){
        this.router.navigate(['signin']);
      }
    })
  }
  ngOnInit() {
    
  }

}
