import { Component, OnInit } from '@angular/core';

import { AdminserviceService } from 'src/app/adminservice.service';
import { stageUpdate, adminData } from 'src/app/admin.model';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';
import { message } from 'src/app/user.model';

@Component({
  selector: 'app-stostages',
  templateUrl: './stostages.component.html',
  styleUrls: ['./stostages.component.scss']
})
export class StostagesComponent implements OnInit {
  stageName: string;
  totalTokens: string;
  tokenValuePerETH: number;
  tokenValuePerBTC: number;
  minTrans: number;
  maxTrans: number;
  hardCap: number;
  startDate: Date;
  endDate: Date;
  baseBonus: number;
  amount1: number;
  bonus1: number;
  amount2: number;
  bonus2: number;
  amount3: number;
  bonus3: number;
  stage: string;
  stageEdit: string;
  currentStage:string;
  
  extrabonus: Array<{amount:number,bonus:number}>=[];
  public toggle:string = '0';
  constructor(private stostage: AdminserviceService, private route: ActivatedRoute) { }
  stageupd: stageUpdate;
  stageupdateresponse: message;
  stageupdate(){
    var extra1={
      amount: this.amount1,
      bonus: this.bonus1
    }
    var extra2={
      amount: this.amount2,
      bonus: this.bonus2
    }
    var extra3={
      amount: this.amount3,
      bonus: this.bonus3
    }
    console.log(this.extrabonus);
    this.extrabonus.pop();
    this.extrabonus.pop();
    this.extrabonus.pop();
    this.extrabonus.push(extra1);
    this.extrabonus.push(extra2);
    this.extrabonus.push(extra3);
    this.stageupd={
      adminId: JSON.parse(window.localStorage.getItem('admin')).adminId,
      stageEdit: this.stage,
      stageName: this.stageName,
      totalTokens: this.totalTokens,
      tokenValuePerETH: this.tokenValuePerETH,
      tokenValuePerBTC: this.tokenValuePerBTC,
      minTrans: this.minTrans,
      maxTrans: this.maxTrans,
      hardCap: this.hardCap,
      startDate: this.startDate,
      endDate: this.endDate,
      baseBonus: this.baseBonus,
      extrabonus: this.extrabonus,
      currentStage: this.currentStage
    }
    // console.log(this.stageupd)
    this.stostage.updateStoStage(this.stageupd).subscribe(res=>{
      this.stageupdateresponse = res;
      this.stostage.getAdminStageDetails(this.stage).subscribe(res=>{
        this.stageDetails = res;
        console.log( res);
        this.totalTokens = this.stageDetails.totalTokens;
        this.tokenValuePerETH = this.stageDetails.tokenValuePerETH;
        this.tokenValuePerBTC = this.stageDetails.tokenValuePerBTC; 
        this.minTrans = this.stageDetails.minTrans;
        this.maxTrans = this.stageDetails.maxTrans;
        this.hardCap = this.stageDetails.hardCap;
        this.startDate = this.stageDetails.startDate;
        this.endDate = this.stageDetails.endDate;
        this.baseBonus = this.stageDetails.baseBonus;
        this.amount1 = this.stageDetails.extraBonus[0].amount;
        this.bonus1 = this.stageDetails.extraBonus[0].bonus;
        this.amount2 = this.stageDetails.extraBonus[1].amount;
        this.bonus2 = this.stageDetails.extraBonus[1].bonus;
        this.amount3 = this.stageDetails.extraBonus[2].amount;
        this.bonus3 = this.stageDetails.extraBonus[2].bonus;
      })
      this.toggle = '0';
      
    });
  }
  formtoggle(){
    this.toggle = '1';
  }
  stageDetails:adminData
  ngOnInit() {this.route
    .queryParamMap
    .pipe(map(params => params.get('stage')))
    .subscribe(res=>{
      this.stage=res;
      
    });
    
    
    this.stostage.getAdminStageDetails(this.stage).subscribe(res=>{
      this.stageDetails = res;
      console.log( res);
      this.totalTokens = this.stageDetails.totalTokens;
      this.tokenValuePerETH = this.stageDetails.tokenValuePerETH;
      this.tokenValuePerBTC = this.stageDetails.tokenValuePerBTC; 
      this.minTrans = this.stageDetails.minTrans;
      this.maxTrans = this.stageDetails.maxTrans;
      this.hardCap = this.stageDetails.hardCap;
      this.startDate = this.stageDetails.startDate;
      this.endDate = this.stageDetails.endDate;
      this.baseBonus = this.stageDetails.baseBonus;
      this.amount1 = this.stageDetails.extraBonus[0].amount;
      this.bonus1 = this.stageDetails.extraBonus[0].bonus;
      this.amount2 = this.stageDetails.extraBonus[1].amount;
      this.bonus2 = this.stageDetails.extraBonus[1].bonus;
      this.amount3 = this.stageDetails.extraBonus[2].amount;
      this.bonus3 = this.stageDetails.extraBonus[2].bonus;
    })
  }

}
