import { Component, OnInit, } from '@angular/core';
import { AuthenticateService } from '../authenticate.service';
import { Router } from '@angular/router'
import { ActivatedRoute,Event,
  NavigationCancel,
  NavigationEnd,
  NavigationError,
  NavigationStart, } from '@angular/router';
import { routerNgProbeToken } from '@angular/router/src/router_module';
import {  allUserDetails, allCompanyDetails, userData } from '../user.model';
import { ShareddataService } from '../services/shareddata.service';
import { BnNgIdleService } from 'bn-ng-idle';
@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {
  loading = false;
  barPercent = 0;
  displayBarPercent: number;
  constructor(private authenticationservice: AuthenticateService, private router: Router, private route: ActivatedRoute, private savedata: ShareddataService,private bnIdle: BnNgIdleService) {
    this.bnIdle.startWatching(300).subscribe((res) => {
      if(res) {
          // console.log("session expired");
          window.localStorage.removeItem('user');
          
          this.router.navigate(['/sessionexpired']);
      }
    })
    this.router.events.subscribe((event: Event) => {
      switch (true) {
        case event instanceof NavigationStart: {
          this.loading = true;
          break;
        }
        case event instanceof NavigationEnd:
        case event instanceof NavigationCancel:
        case event instanceof NavigationError: {
          this.loading = false;
          break;
        }
        default: {
          break;
        }
      }
    });
   }
  public var:string = null;
  public id:string=null;
  profilecompletionreturn: allUserDetails
  allcompanydetailsreturn: allCompanyDetails
  userdetails: allUserDetails
  ngOnInit() {
    // this.route.queryParamMap.subscribe(queryParam=> {
    //   this.id = queryParam.get("id");
    //   window.localStorage.setItem('id',JSON.stringify({id: this.id}));
    // })
    // var person = JSON.parse(window.localStorage.getItem('user'));
    // if(!person){
    //   this.router.navigate(['/signin'])
    // }
    
    // this.authenticationservice.getAllUserDetails().subscribe(res=>{
    //   this.profilecompletionreturn = res;
    //   this.savedata.saveUserData(this.profilecompletionreturn);
    //   console.log(res);
      
    // })
    this.savedata.getAllUserrDetails().subscribe(res=>{
      this.userdetails = res;
      
      console.log(res);
      
    })
    this.authenticationservice.getAllCompanyDetails().subscribe(res=>{
      this.allcompanydetailsreturn = res;
     
      console.log(res);
    })
  }

}
