import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SignupComponent } from './signup/signup.component';
import { SigninComponent } from './signin/signin.component';
import { LayoutComponent } from './layout/layout.component';
import { DashboardComponent } from './layout/dashboard/dashboard.component';
import { TokenComponent } from './layout/token/token.component';
import { TransactionComponent } from './layout/transaction/transaction.component';
import { ProfileComponent } from './layout/profile/profile.component';
import { ViewprofComponent } from './layout/viewprof/viewprof.component';
import { HelpdeskComponent } from './layout/helpdesk/helpdesk.component';
import { AddticketComponent } from './layout/addticket/addticket.component';
import { CompanytokenComponent } from './layout/companytoken/companytoken.component';
import { AdmlayoutComponent } from './admlayout/admlayout.component';
import { AdmdashboardComponent } from './admlayout/admdashboard/admdashboard.component';
import { AdmtransactionsComponent } from './admlayout/admtransactions/admtransactions.component';
import { KyclistComponent } from './admlayout/kyclist/kyclist.component';
import { UserlistComponent } from './admlayout/userlist/userlist.component';
import { AdmsigninComponent } from './admsignin/admsignin.component';
import { AdmsignupComponent } from './admsignup/admsignup.component';
import { TicketsComponent } from './admlayout/tickets/tickets.component';
import { MobileverificationComponent } from './signup/mobileverification/mobileverification.component';
import { MobileotpComponent } from './signup/mobileotp/mobileotp.component';
import { ForgotpasswordComponent } from './signin/forgotpassword/forgotpassword.component';
import { AuthGuard } from './auth.guard';
import { PathnotfoundComponent } from './pathnotfound/pathnotfound.component';
import { AdminprofileComponent } from './admlayout/adminprofile/adminprofile.component';
import { AdminprofilesetupComponent } from './admlayout/adminprofilesetup/adminprofilesetup.component';
import { StostagesComponent } from './admlayout/stostages/stostages.component';
import { StostageselectComponent } from './admlayout/stostageselect/stostageselect.component';
import { SessionexpiredComponent } from './sessionexpired/sessionexpired.component';

const routes: Routes = [
  {  path: 'signup', component: SignupComponent                           },
  {  path: 'signin',component: SigninComponent,                           },
  {  path: 'forgotpass', component: ForgotpasswordComponent               },
  // {  path: '',redirectTo: '/signin',pathMatch: 'full'           },
  
  {  path: 'mobileverify',component: MobileverificationComponent          },
  {  path: 'mobileotp',component: MobileotpComponent                      },
  {  path:'adminsignin',component: AdmsigninComponent                     },
  {  path: 'sessionexpired', component: SessionexpiredComponent   },
  {  path: 'adminsignup',component: AdmsignupComponent                    },
  {  path: 'companytoken', component: CompanytokenComponent               },
  {  path: '',component: LayoutComponent,
     children: [
          {  path: '', redirectTo: '/dashboard',pathMatch: 'full'          },
          {  path: 'dashboard', component: DashboardComponent              },
          {  path: 'token', component: TokenComponent                      },
          {  path:'transaction', component : TransactionComponent          },
          {  path: 'profile', component: ProfileComponent                  },
          {  path: 'viewprof', component: ViewprofComponent                },
          {  path: 'helpdesk', component: HelpdeskComponent                },
          {  path: 'addticket', component: AddticketComponent              }
     ]
  },
  {  path: 'admin', component: AdmlayoutComponent,
    children: [
          {  path: '', redirectTo: '/admin/admindashboard',pathMatch: 'full'  },
          {  path: 'admindashboard', component: AdmdashboardComponent         },        
          {  path: 'transaction',component: AdmtransactionsComponent          },
          {  path: 'kyclist', component: KyclistComponent                     },
          {  path: 'userlist', component: UserlistComponent                   },
          {  path: 'tickets', component: TicketsComponent                     },
          {  path: 'adminprofile', component:AdminprofileComponent            },
          {  path: 'profilesetup', component: AdminprofilesetupComponent      },
          {  path: 'stostages', component: StostagesComponent                 },
          {  path: 'stageselect', component: StostageselectComponent          },
     ]
  },
  { path: '**', component: PathnotfoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
