import { Component, OnInit } from '@angular/core';
import { AdminserviceService } from 'src/app/adminservice.service';
import { userNumber, tokenSales, amountCollected, adminTransaction } from 'src/app/admin.model';

@Component({
  selector: 'app-admdashboard',
  templateUrl: './admdashboard.component.html',
  styleUrls: ['./admdashboard.component.scss']
})
export class AdmdashboardComponent implements OnInit {

  constructor(private dashboard: AdminserviceService) { }
  userno: userNumber;
  tokensales: tokenSales;
  tokenCollected: amountCollected;
  adminTransact: Array<adminTransaction>;
  ngOnInit() {
    this.dashboard.getUsers().subscribe(res=>{
      this.userno=res;
      console.log(res);
      
    });
    this.dashboard.getTokenSales().subscribe(res=>{
      this.tokensales = res;
      console.log(res);
      
    });
    this.dashboard.getAmountCollected().subscribe(res=>this.tokenCollected = res);
    this.dashboard.getAdminTransaction().subscribe(res=>{
      this.adminTransact = res;
      console.log(res);
    });
  }

}
