import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroupDirective, NgForm, Validators,ReactiveFormsModule} from '@angular/forms';
import { MatDialog, MatDialogConfig, MatDialogRef } from '@angular/material';
import {ErrorStateMatcher} from '@angular/material/core';
import { AdminserviceService } from '../adminservice.service';
import { returnmessage } from '../admin.model';


export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

export class passworderrormatcher implements ErrorStateMatcher{
  isErrorState(control: FormControl| null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted= form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted))
  }
}

@Component({
  selector: 'app-admsignup',
  templateUrl: './admsignup.component.html',
  styleUrls: ['./admsignup.component.scss']
})
export class AdmsignupComponent implements OnInit {

  companyname:string;
  companyemail:string;
  password:string;
  confirm_password:string;

  x:string;
  passmatch:number=0;
  ischecked:boolean=false;
  newcompany:{name: string;email: string;password:string;confirm_password:string;}
  message:returnmessage;

  constructor(private adminservice: AdminserviceService) { }

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)
  ]);
  matcher = new MyErrorStateMatcher();

  passwordformcontrol= new FormControl('',[
    Validators.required,
    Validators.minLength(6),
    Validators.maxLength(12),
    
    Validators.pattern(/(?=^.{8,16}$)(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&amp;*()_+}{&quot;:;'?/&gt;.&lt;,])(?!.*\s).*$/)
  ])
  passwordmatcher= new passworderrormatcher();

  check(){
    if(this.password == this.confirm_password){
      this.x = "matching";
      this.passmatch = 1;
    }
    else{
      this.x = "not-matching";
      this.passmatch = 0;
    }
    }

  togglechecked(){
      if(this.ischecked==false)
      {
        this.ischecked=true;
      }
      else 
      {
        this.ischecked=false;
      }
    }

    onSubmit(){
        this.newcompany={
        name: this.companyname,
        email: this.companyemail,
        password: this.password,
        confirm_password: this.confirm_password
      };
  
    this.adminservice.postSignupData(this.newcompany)
    .subscribe(res=>{
      this.message = res;
      console.log(res);
    });
    
    }

  ngOnInit() {
  }

}
