import { Injectable } from '@angular/core';
import { HttpClient, HttpEvent, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class UploadService {
  private home_url: string = "http://192.168.1.113:3000/home/profilepic"; 
  constructor(private http: HttpClient) { }
  private person = JSON.parse(window.localStorage.getItem('user'));
  pushFileToStorage(file: File): Observable<HttpEvent<{}>> {
    const formdata: FormData = new FormData();

    formdata.append('file', file);

    const req = new HttpRequest('POST', 'http://192.168.1.113:3000/home/addimage/'+this.person.email,formdata, {
      reportProgress: true,
      responseType: 'text'
    });

    return this.http.request(req);
  }

  // getFiles(): Observable<any> {
  //   return this.http.get('http://localhost:8080/api/files/all');
  // }
}
