import { Component, OnInit } from '@angular/core';
import { AuthenticateService } from 'src/app/authenticate.service';
import { updateAdminDetails } from 'src/app/admin.model';
import { AdminserviceService } from 'src/app/adminservice.service';


@Component({
  selector: 'app-adminprofilesetup',
  templateUrl: './adminprofilesetup.component.html',
  styleUrls: ['./adminprofilesetup.component.scss']
})
export class AdminprofilesetupComponent implements OnInit {

  constructor(private profilesetup: AdminserviceService) { }
  adminId: string;
  tokenName: string;
  tokenSymbol: string;
  decimalMin: string;
  decimalMax: string;
  referral: string;
  promoCode: string;
  eth: string;
  btc: string;
  adminDetails: updateAdminDetails;
  sendAdminInfo(){
    this.adminDetails={
      adminId: JSON.parse(window.localStorage.getItem('admin')).adminId,
      tokenName: this.tokenName,
      tokenSymbol: this.tokenSymbol,
      decimalMin: this.decimalMin,
      decimalMax: this.decimalMax,
      referral: this.referral,
      promoCode: this.promoCode,
      eth: this.eth,
      btc: this.btc
    }
    this.profilesetup.sendAdminDetails(this.adminDetails).subscribe()
  }
  ngOnInit() {
  }

}
