import { Component, OnInit, Inject } from '@angular/core';
import { AuthenticateService } from '../authenticate.service';
import { Login,msg, person, company } from '../user.model'
import { Router, ActivatedRoute } from '@angular/router'
import {FormControl, FormGroupDirective, NgForm, Validators,ReactiveFormsModule} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';
import { MatDialog, MatDialogConfig, MatDialogRef,MAT_DIALOG_DATA } from '@angular/material';
import { map } from 'rxjs/operators';


export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

export class passworderrormatcher implements ErrorStateMatcher{
  isErrorState(control: FormControl| null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted= form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted))
  }
}

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit {
  email: string;
  password: string;
  person:person;
  passwordresetmsg: msg;
  token: string;
  public message:msg = {err: null,msg: null,user: null,email: null};
  loguser: Login={
    email: ' ',
    password: ' '
  };

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)
  ]);
  matcher = new MyErrorStateMatcher();

  passwordformcontrol= new FormControl('',[
    Validators.required,
    Validators.minLength(6),
    Validators.maxLength(12),
    
    Validators.pattern(/(?=^.{8,16}$)(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&amp;*()_+}{&quot;:;'?/&gt;.&lt;,])(?!.*\s).*$/)
  ])
  passwordmatcher= new passworderrormatcher();
  

  constructor(private route: ActivatedRoute,private _signUpservice: AuthenticateService, private router: Router, private dialog: MatDialog ) { }
  company: company
  loginSubmit(){
    
    this.loguser={
      email: this.email,
      password: this.password
      
    };
    this._signUpservice.Loginuser(this.loguser)
  .subscribe(res=> {
    this.message = res;
    if(res.msg == '1'){
      this.person= {
        name: res.user,
        email: res.email,
      }
      this.company={
        companyId: '111111'
      }
      window.localStorage.clear();
      window.localStorage.setItem('user',JSON.stringify(this.person));
      window.localStorage.setItem('company',JSON.stringify(this.company));
      this.openDialog();
      setTimeout(()=>{this.router.navigate(['/dashboard']);},3000)
      
    }else{
      this.router.navigate([''])
    }
  }
    );
  
  }
  keyDownFunction(event) {
    if(event.keyCode == 13) {
      this.loginSubmit();
    }
  }
  ngOnInit() {
    window.localStorage.clear();
    this.email="";
    this.password="";

    this.route
      .queryParamMap
      .pipe(map(params => params.get('token')))
      .subscribe(res=>{
        console.log(res);
        this.token= res;
      });
      if(this.token)
      {
        console.log("token found..");
      }
      else
      {
        console.log("no token");
      }
  }

  forgotpassword(){
    this.openfrogotpassDialog("getemail");
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(LoginDialog, {
      width: '500px',
      data: {name: this.person.name, msg: "setting up your dashboard"}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed'+result);
    });
  }

  openfrogotpassDialog(message: string): void {
    const dialogRef = this.dialog.open(forgotpassDialog, {
      width: '500px',
      height: '300px',
      data: {email: this.email,msg: message}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result){
        this.openfrogotpassDialog("emailentered");
        console.log('The dialog was closed'+result);
        this._signUpservice.resetpassword(result).subscribe(res=>{
          this.passwordresetmsg=res;
          console.log(res);
        })
      }
      else
      console.log('cancelled');
    });
  }

}


@Component({
  selector: 'login-dialog',
  templateUrl: 'login-dialog.html',
})
export class LoginDialog {

  constructor(
    public dialogRef: MatDialogRef<LoginDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {name: string,msg: string}) {}
    

  onNoClick(): void {
    this.dialogRef.close();
  }

  closedialog(){
    setTimeout(()=>{this.onNoClick();},3000)
  }
}

@Component({
  selector: 'login-dialog',
  templateUrl: 'forgotpass-dialog.html',
})
export class forgotpassDialog {

  constructor(
    public dialogRef: MatDialogRef<forgotpassDialog>,
    @Inject(MAT_DIALOG_DATA) public data: {email: string, msg: string}) {}
    

  onNoClick(): void {
    this.dialogRef.close();
  }

  closedialog(){
    setTimeout(()=>{this.onNoClick();},5000)
  }

}
